<template>
  <div>
    <h2>后台数据</h2>
    <p>分类：{{ classify }}</p>
    <p>文本：{{ texts }}</p>
  </div>
</template>

<script>
export default {
  name: 'MyComponent',
  data() {
    return {
      classify: '',
      texts: ''
    }
  },
  mounted() {
    // 向后台发送请求
    fetch('http://127.0.0.1:8000/text/1/', {
      method: 'POST'
    })
    .then(response => response.json())
    .then(data => {
      // 更新组件数据
      this.classify = data.classify;
      this.texts = data.texts;
    })
    .catch(error => console.log(error));
  }
}
</script>
