<template>
  <div id="children">
    <p>{{item.name}}</p>
    <p>{{item.age}}</p>
    <p>{{item.gender}}</p>
    <p></p>
    <p></p>
    <button @click="bthHandle">改变测试</button>
  </div>
</template>

<script>
export default {
  name: 'Children',
  props: ['item'],//获取父组件传值
  data(){
    return {

    }
  },
  created(){
    console.log(this.item)
  },
  methods:{
    bthHandle(){
      console.log('点击了子组件')
      // 通过触发自定义事件修改父组件传递的text
      this.item.gender = '你猜';
      this.$emit('event1','我修改了text')
    }
  }
}
</script>

